import React, {Component} from 'react'
import Todoitem from './components/Todoitems/Todoitems'
import Additems from './components/Additem/Additem'

class App extends Component {
  state={
    items : [
      {id : 1, name : "Mahmoud", age : 24},
      {id : 2, name : "Ahmed", age : 24},
      {id : 3, name : "Weza", age : 24}
    ]
  }
  Deleteitem = (id) => {
    let locitems = this.state.items;
    let ii = locitems.findIndex(i => i.id === id)
    locitems.splice(ii,1)
    this.setState({items:locitems})
  }

  Additem = (item) => {
    item.id = Math.random()
    let loc2items = this.state.items;
    loc2items.push(item)
    this.setState({
      items : loc2items
    })

  }

  render() {
    return (
        <div className="App container">
            <h1 className="text-center">TodoList</h1>
            <Todoitem items={this.state.items} Deleteitem={this.Deleteitem} />
            <Additems additem={this.Additem}/>
        </div>
    )
  }
}

export default App;
